#rate * {
  position: relative;
  margin: 0;
  padding: 0;
}

#rate {
  float: left;
  height: 25px; /* Set maximum height to 25px */
  padding: 0 10px;
}

#rate:not(:checked) > input {
  position: absolute;
  top: -9999px;
}

#rate:not(:checked) > label {
  float: right;
  width: 1em;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  font-size: 25px; /* Adjust font size to match maximum height */
  color: #ccc;
  max-width: 145px; /* Set maximum width to 145px */
}

#rate:not(:checked) > label:before {
  content: "â˜… ";
}

#rate > input:checked ~ label {
  color: #ffc700;
}

#rate:not(:checked) > label:hover,
#rate:not(:checked) > label:hover ~ label {
  color: #deb217;
}

#rate > input:checked + label:hover,
#rate > input:checked + label:hover ~ label,
#rate > input:checked ~ label:hover,
#rate > input:checked ~ label:hover ~ label,
#rate > label:hover ~ input:checked ~ label {
  color: #c59b08;
}
